<template>
    <div class="header">
       <div>
            <router-link v-if="title!=='首页'" to='/' class="iconfont icon-jiantouarrowhead7"></router-link>
            <router-link v-else to='/search' class="iconfont icon-sousuo"></router-link>
       </div>
       <h1 class="header-title">{{title}}</h1>
       <div class="header-register">
            <p v-if='!$store.state.isEnter'>
                <router-link v-if='!login_phone' to="/login/signin_pwd" >登录</router-link>
                <router-link v-else to="/login/signin_phone">手机登陆</router-link>
                <router-link to="/login/register">注册</router-link>
            </p>
            <p v-else>
                <router-link to="/user" class="iconfont icon-wode"></router-link> 
            </p>
       </div>
    </div>
</template>

<script>
export default{
    name:"elmheader",
    props:["fatherName"],
    data() {
        return {
            msg:"测试数据",
            title:this.fatherName,
            login_phone:false,

        }
    },
    created() {
        if(this.fatherName==="登录"){
            this.login_phone=true;
        }
    },
    watch:{
        fatherName:function(newVal,oldVal) {
         this.title=newVal;
        }
    },
    methods: {
        
    },
}

</script>

<style scoped> 
    .header{
        display: flex;
        justify-content: space-between;
        align-items: center;
        width:100%;
        height:0.9rem;
        background: rgb(16, 166, 204);
        padding:0 0.1rem;
        color:#fff;
    }
    .header a,h1{
        font-size:0.34rem;
        color:#fff;
    }
    .header .iconfont{
        font-size:0.5rem;
    }
    .header-register a:first-child{
        position: relative;
        padding-right:0.1rem;
    }
    .header-register a:first-child::after{
        position: absolute;
        top:0.1rem;
        right:0;
        content:'';
        width:0.02rem;
        height:0.28rem;
        background:#fff;
    }

</style>


