<template>
  <div class="hello">
    <ul>
      <li v-for='(item,index) in menu' :key='index' :class="{active:(isActive===index)}" @click='setClass(index)'>{{item}}</li>
    </ul>

    <elm-footer></elm-footer>

  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: '欢迎来带vue项目',
      menu:[1,2,3,4],
      isActive:null,
    }
  },
  methods: {
    setClass:function(i){
      this.isActive=i;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.active{
  color:red;
}
</style>



